@font-face {
  font-family: "fontawesome2";
  src: url('../../fonts/CRO55DM.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "fontawesome2", sans-serif;
}

html, body {
  height: 100%;
  margin: 0;
}

.container_img {
  position: fixed;
  width: 100%;
  height: 100%;
}

.background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  z-index: -1;
}

.header-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  max-width: auto;
  display: flex;
  background-color: #8A5286;
  justify-content: space-between;
  z-index: 1;
  box-shadow: 0 4px 8px rgba(181, 182, 186, 1);
  flex-wrap: nowrap;
  flex-direction: row;
  align-content: space-around;
}

.header-logo button {
  background-color: transparent;
  padding: 10px 10px;
  border: none;
  border-radius: 30px 30px 0 0;
  border-bottom: 2px solid rgba(163, 169, 177, 1);
  cursor: pointer;
  flex: 1;
}
.header-logo button:hover {
   background-color: rgba(201, 214, 228, 1);
}

.header-navigation {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 30px;
}

.logo img {
  max-width: 100%;
  height: 100%;

}

.header-nav-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header-nav-item img {
  max-width: 100%;
  height: 100%;
  padding: 1px;
}

.header-nav-item h2 {
  font-size: 34px;
  font-weight: 700;
  text-align: center;
  margin: 0;
}

.header-nav-button {
  padding: 10px 10px;
  border: none;
  background-color: transparent;
  border-bottom: 2px solid rgba(163, 169, 177, 1);
  color: rgba(201, 214, 228, 1);
  cursor: pointer;
  border-radius: 30px 30px 0 0;
}

.header-nav-button:hover {
  background-color: rgba(201, 214, 228, 1);
  color: rgba(111, 114, 117, 1);
}